@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,500;0,700;1,300&display=swap');

*
{
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
  color: #3d3d3d;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.wallpaper
{
  height: 100vh;
  overflow: hidden;
  background-image: url("./images/walpaper.jpg");
  background-size: 100%;
  background-position:center;

  position: relative;
}

.task-bar
{
  height: 45px;
  width: 100%;
  padding: 10px 15px ;
  padding-left: 0;
  padding-right: 0;
  background-color: rgba(255, 255, 255, 0.7);

  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;

}

.right-tools
{
  position: absolute;
  right: 0;

  display: flex;
  align-items: center;
  justify-content: center;

}

.icons
{
  font-size: 20px;
  padding:0 5px;
}

.time-date
{
  padding: 0 5px;
  margin-right: 10px;
  text-align: right;
}