@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,500;0,700;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*
{
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
  color: #3d3d3d;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wallpaper
{
  height: 100vh;
  overflow: hidden;
  background-image: url(/windows-11/static/media/walpaper.7e2fb162.jpg);
  background-size: 100%;
  background-position:center;

  position: relative;
}

.task-bar
{
  height: 45px;
  width: 100%;
  padding: 10px 15px ;
  padding-left: 0;
  padding-right: 0;
  background-color: rgba(255, 255, 255, 0.7);

  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;

}

.right-tools
{
  position: absolute;
  right: 0;

  display: flex;
  align-items: center;
  justify-content: center;

}

.icons
{
  font-size: 20px;
  padding:0 5px;
}

.time-date
{
  padding: 0 5px;
  margin-right: 10px;
  text-align: right;
}
.primary-heading
{
    font-size: 16px !important;
    font-weight: 700 !important;
}

.secondary-heading
{
    font-size: 14px !important;
    font-weight: 500 !important;
}

/* Margin */
.mt-30
{
    margin-top: 30px !important; 
}

.mt-60
{
    margin-top: 60px !important;
}

.paragraph
{
    font-size: 13px;
}

.font-bold
{
    font-weight: 700;
}
.imageBox
{
    height: 40px;
    width: 40px;
    overflow: hidden;
    padding: 0 10px;
}

.image
{
    width: 100%;
    /* height: 100%; */
    object-fit: cover;
}
.pinned-item
{
    width: calc(100% / 6);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.pinned-image
{
    max-width: 50px;
    height: 50px;
    margin-bottom: 5px;
}
.windows-box {
    max-height: 700px;
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 10px;
    overflow: hidden;
  
    position: absolute;
    left: 50%;
    bottom: -100%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  
    opacity: 0;
    visibility: hidden;
    transition: 0.5s ease;
  }
  
  .tools-box {
    padding: 30px 25px;
    width: 98%;
    max-width: 650px;
  }
  
  .active {
    bottom: 80px;
  
    opacity: 1;
    visibility: visible;
  }
  
  .heading {
    padding: 0 25px;
  }
  
  .pinned-box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .recommended-box {
    padding: 0 25px;
  
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .profile-box {
    background-color: rgba(210, 214, 207, 0.7);
    padding: 15px 50px;
  
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .profile {
    display: flex;
    align-items: center;
  }
  .profile-image {
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .poweroff-icon {
    font-size: 18px;
  }
  
.recommended-item-image
{
    width: 50px;
    height: 50px;
    margin-right: 15px;
}

.recommended-item-box
{
    width: calc(100% / 2);
    display: flex;
    align-items: center;
}
